<template>
    <div id="bccContainer">
        <div class="header flex flex-align-center flex-pack-between">
            <div class="flex flex-align-center">
              <img src="../../../static/assets/logo1.png" alt="">
              <span class="u-f bold">{{currentLang.lang2}}</span>
            </div>
            <div class="u-f Back" @click="jumpHome">{{currentLang.lang61}}</div>
        </div>
        <div class="contanier">
            <!--收益-->
            <div class="rank1 flex flex-pack-between">
                <div class="flex flex-align-center">
                    <h1 style="padding-right:10px;">{{currentLang.lang62}}</h1>
<!--                    <h1 style="padding-left:0;padding-right:15px;">statistics</h1>-->
                </div>
<!--                <div class="flex flex-align-center">-->
<!--                    <h1>Today data</h1>-->
<!--                    <h1 style="padding-left: 10px;"><span id="dayM">0</span><i-->
<!--                            style="font-style:normal;padding-left:3px;">ETH</i></h1>-->
<!--                </div>-->
            </div>
            <div class="incomeBox">
                <div class="incomeHeader flex flex-align-center">
                    <div class="addr w35">
                        <div class="u-blue">{{currentLang.lang63}}</div>
                        <div class="mt15">{{showData.adr}}</div>
                    </div>
                    <div class="totalIn w15">
                        <div class="u-blue">{{currentLang.lang64}}</div>
                        <div class="mt15">{{showData.tzNum | formatNum}}<i
                                style="font-size:13px;font-style:normal;padding-left:2px;">ETH</i></div>
                    </div>
                    <div class="totalIn w15">
                        <div class="u-blue">{{currentLang.lang65}}</div>
                        <div class="mt15">{{showData.tzzonge | formatNum}}<i
                                style="font-size:13px;font-style:normal;padding-left:2px;">ETH</i></div>
                    </div>
                    <div class="TeamNum w10">
                        <div class="u-blue">{{currentLang.lang66}}</div>
                        <div class="mt15">{{showData.tuandui}}</div>
                    </div>

                    <div class="TeamData w20">
                        <div class="u-blue">{{currentLang.lang67}}</div>
                        <div class="mt15">{{showData.yeji |formatNum}} <i
                                style="font-size:13px;font-style:normal;padding-left:2px;">ETH</i></div>
                    </div>
                    <div class="level w10">
                        <div class="u-blue">{{currentLang.lang68}}</div>
                        <div class="mt15">{{showData.lev==1?"V1":showData.lev==2?"V2":showData.lev==3?"V3":0}}</div>
                    </div>
                    <div class="svLevel w10">
                        <div class="u-blue">{{currentLang.lang69}}</div>
                        <div class="mt15">{{showData.svLev == 1?'SV1':showData.svLev == 2?'SV2':0}}</div>
                    </div>
                </div>
            </div>
            <!--等级1-->
            <div class="levelBox">
                <h1 class="rank1 textLeft">{{currentLang.lang70}}</h1>
                <div class="flex levelHeader">
                    <div class="w50">{{currentLang.lang71}}</div>
                    <div class="w50">{{currentLang.lang72}}</div>
                </div>
                <div class="levelBody">
                    <div class="flex levelItem flex-align-center">
                        <div class="w50">V1</div>
                        <div class="w50">{{showData.v1Num}}</div>
                    </div>
                    <div class="flex levelItem flex-align-center">
                        <div class="w50">V2</div>
                        <div class="w50">{{showData.v2Num}}</div>
                    </div>
                    <div class="flex levelItem flex-align-center">
                        <div class="w50">V3</div>
                        <div class="w50">{{showData.v3Num}}</div>
                    </div>
                </div>

            </div>

        </div>
    </div>
</template>

<script>
    import Web3 from "web3";
    import BigNumber from "bignumber.js";
    // 加载语言
    export default {
        name: 'MyData',
        data: function () {
            return {
                currentAdr: 0,
                langName: "",
                currentLang:"",
                web3: null,
                DbAbi: ItemAbi.abi,
                TzAbi: ItemAbi.abi2,
                dbAddress: ItemAbi.hyAddress,
                tzAddress: ItemAbi.tphyAddress,
                showData: {
                    adr: "",
                    tzNum: 0,
                    tzzonge: 0,
                    tuandui: 0,
                    yeji: 0,
                    lev: 0,
                    svLev: 0,
                    v1Num: 0,
                    v2Num: 0,
                    v3Num: 0
                }
            }
        },
        filters: {
            formatNum(val) {
                return Number(val).toFixed(6)
            },
            formatNum5(val) {
                return Number(val).toFixed(5)
            },
            formatNum4(val) {
                return Number(val).toFixed(4)
            },
            formatNum2(val) {
                return Number(val).toFixed(2)
            }
        },
        async created() {
          var that = this;
          if (localStorage.getItem('lang')) {
            var langStorage = localStorage.getItem('lang');
            if (langStorage == 'ko') {
              that.currentLang = lang.ko;
            } else if (langStorage == 'en') {
              that.currentLang = lang.en;
            } else {
              that.currentLang = lang.si;
            }
          } else {//没有设置语言
            that.currentLang = lang.si;
          }
            this.initWow();
            //  初始化 web3及账号
            await this.initWeb3Account();
        },
        mounted() {
        },
        methods: {
            async initWeb3Account() {
                var that = this;
                if (window.ethereum) {
                    this.provider = window.ethereum;
                    try {
                        await window.ethereum.enable();
                    } catch (error) {
                        //   console.log("User denied account access");
                    }
                } else if (window.web3) {
                    this.provider = window.web3.currentProvider;
                } else {
                    this.provider = new Web3.providers.HttpProvider("https://mainnet.infura.io");
                }
                this.web3 = new Web3(this.provider);
                that.DbHy = new this.web3.eth.Contract(that.DbAbi, that.dbAddress, {});
                that.TzHy = new this.web3.eth.Contract(that.TzAbi, that.tzAddress, {});
                that.getInfo();
                setInterval(function () {
                    that.getInfo();
                }, 3000);
            },
            initWow: function () {
                var wow = new WOW({
                    boxClass: 'wow',
                    animateClass: 'animated',
                    offset: 0,
                    mobile: true,
                    live: true
                });
                wow.init();
            },
            getInfo: function () {
                var that = this;
                this.web3.eth.getAccounts().then(accs => {
                    that.showData.adr = accs[0];
                    that.getPageInfo()
                })
            },
            getPageInfo: function () {
                var that = this;
                that.DbHy.methods.getSv(that.showData.adr).call().then((res) => {
                    that.showData.svLev = res;
                })
                that.TzHy.methods.usersMap(that.showData.adr).call().then((res) => {
                    that.showData.tzNum = new BigNumber(res.tznum) / 10 ** 18;
                    that.showData.tuandui = res.tuandui;
                    that.showData.tzzonge = new BigNumber(res.tzzonge) / 10 ** 18;
                    that.showData.yeji = new BigNumber(res.yeji) / 10 ** 18;
                    that.showData.lev = res.lev;
                })
                that.TzHy.methods.v1Num().call().then((res) => {
                    that.showData.v1Num = res;
                })
                that.TzHy.methods.v2Num().call().then((res) => {
                    that.showData.v2Num = res;
                })
                that.TzHy.methods.v3Num().call().then((res) => {
                    that.showData.v3Num = res;
                })

            },
            jumpHome: function () {
              this.$router.go(-1);
          },
        }
    }
</script>
<style scoped>
    .header {
        width: 100%;
        height: 80px;
        background: #ff7917;
        box-sizing: border-box;
        padding: 0 20px;
    }
    .Back{
      font-size: 16px;
    }
    .header img {
        height: 50px;
      margin-right: 20px;
    }

    .contanier {
        padding: 40px 20px;
    }

    .rank1 {
        padding-bottom: 15px;
        border-bottom: 1px solid #eee;
    }

    .incomeBox {
        margin-top: 20px;
    }

    .incomeHeader div {
        /*width:16.6%;*/
        font-size: 16px;
        /*color:#1E9FFF;*/
    }

    .incomeHeader {
        margin-bottom: 20px;
    }

    .incomeItem div {
        font-size: 15px;
    }

    .levelBox {
        margin-top: 60px;
    }

    .levelHeader {
        margin-top: 20px;
        color: #ff7917;
        font-size: 16px;
        margin-bottom: 20px;
    }

    .levelItem {
        height: 32px;
        margin-bottom: 10px;
        border-bottom: 1px solid #eee;
        font-size: 15px;
    }

    /*兼容手机端*/
    @media screen and (min-width: 301px) and (max-width: 960px) {
        .contanier {
            padding: 25px 15px;
        }

        .rank1 {
            font-size: 18px;
        }
        .Back{
          font-size: 14px;
        }
        .header {

            height: 50px;

        }

        .addr {
            text-align: left;
        }

        .header img {
            height: 32px;
            margin-right:10px;
        }

        .incomeHeader {
            flex-wrap: wrap;
        }

        .levelBox .w50 {
            text-align: left;
        }

        .addr {
            width: 100%;
            padding-bottom: 10px;
            border-bottom: 1px solid #eee;
        }

        .addr .mt15 {
            font-size: 14px;
            margin-top: 5px;
        }

        .totalIn, .TeamNum, .TeamData, .level, .svLevel {

            width: 100%;
            display: flex;
            justify-content: space-between;
            margin-top: 10px;
            padding-bottom: 10px;
            border-bottom: 1px solid #eee;

        }

        .levelBox {
            margin-top: 28px;
        }

        .totalIn .mt15, .TeamNum .mt15, .TeamData .mt15, .level .mt15, .svLevel .mt15 {
            margin-top: 0;
            font-size: 14px;
        }

        .levelBox .w50:last-child {
            text-align: center;
        }

        .rank1 h1 {
            font-size: 18px;
        }

        div.rank1 .flex {
            flex-direction: column;
        }
    }

    .mt15 {
        margin-top: 15px;
    }

    .u-blue {
        color: #ff7917;
    }
</style>



